
$('.notify-selector').on('click', (event) => {
    // let $this = $(event.currentTarget);

    $('.notify').toggleClass('checked');
});

$(document).on('click', (event) => {
    if (!$(event.target).closest('.notify-selector').length && !$(event.target).hasClass('.notify-selector') && !$(event.target).closest('.notify').length && !$(event.target).hasClass('.notify')) {
        $('.notify').removeClass('checked');
    }
    event.stopPropagation();
});


