function chartInit(object,values,chartColor) {
    let axisXStep = (300 / 11).toFixed(2); //   300  150
    let obj1 = object.get(0);
    let ctx = obj1.getContext("2d");
    let valueMin = values[0].value;
    let valueMid; 
    let valueMax = values[0].value;

    for (let i = 1; i < values.length; i++) {
        if (values[i].value > valueMax) {
            valueMax = values[i].value;
        }

        if (values[i].value < valueMin) {
            valueMin = values[i].value;
        }
    }
    
    //   
    // valueMax = (valueMax / 500).toFixed(0) * 500 + 500;
    // valueMin = (valueMin / 500).toFixed(0) * 500 - 500;
    valueMid = ((valueMax - valueMin) / 2) + valueMin;

    object.parent().parent().find('.chart__value.max').text(valueMax);
    object.parent().parent().find('.chart__value.mid').text(valueMid);
    object.parent().parent().find('.chart__value.min').text(valueMin);
    
    for (let i = 0; i < values.length; i++) {
        object.parent().parent().find('.chart__axis_x .chart__value').eq(i).text(values[i].month);
    }

    ctx.lineWidth = 2; //  
    ctx.moveTo(0, chartValue(values[0].value,150,valueMin,valueMax)); //  ( )

    for (let i = 1; i <= 12; i++) {
        ctx.lineTo(axisXStep * (i - 1), chartValue(values[i - 1].value,150,valueMin,valueMax));
        ctx.strokeStyle = chartColor;
        ctx.stroke();
    }

    function chartValue (
        value, //   
        canvasAxisYSize, //    Y
        axisYStartValue, //     Y
        axisYEndValue //     Y
    ) {
        return(canvasAxisYSize - (value - axisYStartValue) * canvasAxisYSize / (axisYEndValue - axisYStartValue));
    }

    function popUpValue1 (value) {
        return values[value].value;
    }

    function popUpValue2 (value) {
        if (value < 1) {
            return 0;
        }

        return (100 * (popUpValue1(value) - popUpValue1(value - 1)) / popUpValue1(value - 1)).toFixed(2);
    }

    function movementMessage () {
        let x = event.offsetX;
        let pointSector = (x / (object.width() / 11)).toFixed(0);

        if (x < 0) {
            pointSector = 0;
        }

        let pointPositionLeft =  pointSector * (object.width() / 11) + 4;
        let messagePositionLeft =  pointSector * (object.width() / 11) + 10;
        let pointPositionTop = chartValue(values[pointSector].value,object.height(),valueMin,valueMax) + 4;            
        let messagePositionTop = pointPositionTop - 100;    
        let messageValue1 = popUpValue1(pointSector);
        let messageValue2 = popUpValue2(pointSector);
        
        //   
        // -  
        if (messagePositionLeft > object.width() - 150) {
            messagePositionLeft = object.width() - 150;
        }
        // -  
        if (messagePositionTop < -60) {
            messagePositionTop = -60;
        }

        object.parent().find('.canvas-point').css({'top' : pointPositionTop,'left' : pointPositionLeft});
        object.parent().find('.canvas-message').css({'top' : messagePositionTop, 'left' : messagePositionLeft});
        object.parent().find('.canvas-message-1').text(messageValue1);
        object.parent().find('.canvas-message-2').text(messageValue2 + ' %').removeClass('down');
        if (messageValue2 < 0) {
            object.parent().find('.canvas-message-2').addClass('down');
        }
    }
    
    object.on('mouseenter', () => {
        movementMessage();
        object.parent().find('.canvas-popup').addClass('checked');
    });

    object.on('mouseleave', () => {
        object.parent().find('.canvas-popup').removeClass('checked');
    });

    object.on('mousemove', () => {
        movementMessage();
    });
} 

if ($('.chart').length > 0) {
    chartInit(
        $('#financial-chart-1'),
        [
            { month: 'Jan', value: 3000 },
            { month: 'Feb', value: 3400 },
            { month: 'Mar', value: 4500 },
            { month: 'Apr', value: 4000 },
            { month: 'May', value: 3700 },
            { month: 'Jun', value: 3500 },
            { month: 'Jul', value: 4000 },
            { month: 'Aug', value: 4250 },
            { month: 'Sep', value: 4750 },
            { month: 'Oct', value: 5250 },
            { month: 'Nov', value: 5500 },
            { month: 'Dec', value: 5900 },
        ],
        '#1d95d1'
    );

    chartInit(
        $('#financial-chart-2'),
        [
            { month: 'Jan', value: 3000 },
            { month: 'Feb', value: 4000 },
            { month: 'Mar', value: 4500 },
            { month: 'Apr', value: 3700 },
            { month: 'May', value: 3200 },
            { month: 'Jun', value: 3500 },
            { month: 'Jul', value: 4000 },
            { month: 'Aug', value: 4250 },
            { month: 'Sep', value: 4750 },
            { month: 'Oct', value: 5250 },
            { month: 'Nov', value: 5500 },
            { month: 'Dec', value: 5700 },
        ],
        '#1d95d1'
    );
}


